/**
 * Better Auth configuration for JWT-based authentication.
 *
 * Configures Better Auth with JWT provider using shared secret
 * synchronized with backend JWT_SECRET.
 */
import { betterAuth } from "better-auth";
import { createAuthClient } from "better-auth/react";
import { prismaAdapter } from "better-auth/adapters/prisma";
import { prisma } from "./prisma";

// Server-side auth instance (for API routes and server components)
export const auth = betterAuth({
  // Database configuration with Prisma adapter
  database: prismaAdapter(prisma, {
    provider: "postgresql",
  }),

  // JWT provider configuration
  jwt: {
    // Shared secret - MUST match backend JWT_SECRET
    secret: process.env.AUTH_SECRET || "",

    // Token expiration: 24 hours
    expiresIn: "24h",

    // HS256 signing algorithm
    algorithm: "HS256",
  },

  // Email/password authentication
  emailAndPassword: {
    enabled: true,

    // Password requirements
    minPasswordLength: 8,
    requireUppercase: true,
    requireLowercase: true,
    requireNumbers: true,
  },

  // Session configuration
  session: {
    // Store JWT in httpOnly cookie (XSS protection)
    cookieCache: {
      enabled: true,
      maxAge: 60 * 60 * 24, // 24 hours in seconds
    },
  },
});

// Client-side auth instance (for React components)
export const authClient = createAuthClient({
  baseURL: process.env.NEXT_PUBLIC_APP_URL || "http://localhost:3000",
});

// Export authClient as default for convenience
export default authClient;
